.disp{
    color: maroon;
    text-align: center;
    margin-top: 5rem;
    }
    
    .text-block {
       
        position: absolute;
         bottom: 350px ;
        right: 650px;
        color: white;
        padding-left: 100px;
        padding-right: 20px;
        font-size: larger;
      }
      button {
        position: relative;
        display: inline-block;
        margin: 15px;
        padding: 15px 30px;
        text-align: center;
        font-size: 18px;
        letter-spacing: 1px;
        text-decoration: none;
        color: #725AC1;
        background: transparent;
        cursor: pointer;
        transition: ease-out 0.5s;
        border: 4px solid #725AC1;
        border-radius: 10px;
        box-shadow: inset 0 0 0 0 #725AC1;
      }
     
      button:hover {
        color: white;
        box-shadow: inset 0 -100px 0 0 #725AC1;
      }
     
      button:active {
        transform: scale(0.9);
      }
     
      .me-auto{
        font-size: medium;
        font-weight: 400;
        text-decoration: none;
     
      }
     
    
    
    
    